:host {
  display: block;
  position: absolute;
  z-index: 99999;
}

:host([hidden]) {
  display: none;
}

@keyframes keyFrameScaleUp {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes keyFrameScaleDown {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes keyFrameFadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: var(--simple-tooltip-opacity, 1);
  }
}

@keyframes keyFrameFadeOutOpacity {
  0% {
    opacity: var(--simple-tooltip-opacity, 1);
  }
  100% {
    opacity: 0;
  }
}

@keyframes keyFrameSlideDownIn {
  0% {
    transform: translateY(-2000px);
    opacity: 0;
  }
  10% {
    opacity: 0.2;
  }
  100% {
    transform: translateY(0);
    opacity: var(--simple-tooltip-opacity, 1);
  }
}

@keyframes keyFrameSlideDownOut {
  0% {
    transform: translateY(0);
    opacity: var(--simple-tooltip-opacity, 1);
  }
  10% {
    opacity: 0.2;
  }
  100% {
    transform: translateY(-2000px);
    opacity: 0;
  }
}

.fade-in-animation {
  opacity: 0;
  animation-delay: var(--simple-tooltip-delay-in, 0ms);
  animation-name: keyFrameFadeInOpacity;
  animation-iteration-count: 1;
  animation-timing-function: linear;
  animation-duration: var(--simple-tooltip-duration-in, 100ms);
  animation-fill-mode: forwards;
}

.fade-out-animation {
  opacity: var(--simple-tooltip-opacity, 1);
  animation-delay: var(--simple-tooltip-delay-out, 0ms);
  animation-name: keyFrameFadeOutOpacity;
  animation-iteration-count: 1;
  animation-timing-function: linear;
  animation-duration: var(--simple-tooltip-duration-out, 100ms);
  animation-fill-mode: forwards;
}

.scale-up-animation {
  transform: scale(0);
  opacity: var(--simple-tooltip-opacity, 1);
  animation-delay: var(--simple-tooltip-delay-in, 500ms);
  animation-name: keyFrameScaleUp;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
  animation-duration: var(--simple-tooltip-duration-in, 500ms);
  animation-fill-mode: forwards;
}

.scale-down-animation {
  transform: scale(1);
  opacity: var(--simple-tooltip-opacity, 1);
  animation-delay: var(--simple-tooltip-delay-out, 500ms);
  animation-name: keyFrameScaleDown;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
  animation-duration: var(--simple-tooltip-duration-out, 500ms);
  animation-fill-mode: forwards;
}

.slide-down-animation {
  transform: translateY(-2000px);
  opacity: 0;
  animation-delay: var(--simple-tooltip-delay-out, 500ms);
  animation-name: keyFrameSlideDownIn;
  animation-iteration-count: 1;
  animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  animation-duration: var(--simple-tooltip-duration-out, 500ms);
  animation-fill-mode: forwards;
}

.slide-down-animation-out {
  transform: translateY(0);
  opacity: var(--simple-tooltip-opacity, 1);
  animation-delay: var(--simple-tooltip-delay-out, 500ms);
  animation-name: keyFrameSlideDownOut;
  animation-iteration-count: 1;
  animation-timing-function: cubic-bezier(0.4, 0, 1, 1);
  animation-duration: var(--simple-tooltip-duration-out, 500ms);
  animation-fill-mode: forwards;
}

.cancel-animation {
  animation-delay: -30s !important;
}

/* Thanks IE 10. */
.hidden {
  display: none !important;
}

.pf-c-tooltip {
  position: var(--pfe-tooltip--Position, relative);
  max-width: var(--pfe-tooltip--MaxWidth, 300px);
  box-shadow: var(--pfe-tooltip--BoxShadow, rgba(3, 3, 3, 0.12) 0px 4px 8px 0px, rgba(3, 3, 3, 0.06) 0px 0px 4px);
}

.pf-c-tooltip.pf-m-top {
  transform: var(--pfe-tooltip__offset-top--Transform, translateY(calc(-1 * (8px + 9px))));
}

.pf-c-tooltip.pf-m-top .pf-c-tooltip__arrow {
  bottom: var(--pfe-tooltip__arrow--top--Bottom, 0);
  left: var(--pfe-tooltip__arrow--top--Left, 50%);
  transform: var(--pfe-tooltip__arrow--top--Transform, translate(-50%, 50%) rotate(45deg));
}

.pf-c-tooltip.pf-m-bottom {
  transform: var(--pfe-tooltip__offset-bottom--Transform, translateY(calc(1 * (8px + 9px))));
}

.pf-c-tooltip.pf-m-bottom .pf-c-tooltip__arrow {
  left: var(--pfe-tooltip__arrow--bottom--Left, 50%);
  bottom: var(--pfe-tooltip__arrow--bottom--Bottom);
  transform: var(--pfe-tooltip__arrow--bottom--Transform, translate(-50%, -50%) rotate(45deg));
}

.pf-c-tooltip.pf-m-left {
  transform: var(--pfe-tooltip__offset-left--Transform, translateX(calc(-1 * (8px + 9px))));
}

.pf-c-tooltip.pf-m-left .pf-c-tooltip__arrow {
  top: var(--pfe-tooltip__arrow--left--Top, 50%);
  right: var(--pfe-tooltip__arrow--left--Right, 0);
  transform: var(--pfe-tooltip__arrow--left--Transform, translate(50%, -50%) rotate(45deg));
}

.pf-c-tooltip.pf-m-right {
  transform: var(--pfe-tooltip__offset-right--Transform, translateX(calc(1 * (8px + 9px))));
}

.pf-c-tooltip.pf-m-right .pf-c-tooltip__arrow {
  left: var(--pfe-tooltip__arrow--right--Left, 0);
  top: var(--pfe-tooltip__arrow--right--Top, 50%);
  transform: var(--pfe-tooltip__arrow--right--Transform, translate(-50%, -50%) rotate(45deg));
}

.pf-c-tooltip__content {
  position: var(--pfe-tooltip__content--Position, relative);
  padding-top: var(--pfe-tooltip__content--PaddingTop, 16px);
  padding-bottom: var(--pfe-tooltip__content--PaddingBottom, 16px);
  padding-left: var(--pfe-tooltip__content--PaddingLeft, 16px);
  padding-right: var(--pfe-tooltip__content--PaddingRight, 16px);
  font-size: var(--pfe-tooltip__content--FontSize, 14px);
  color: var(--pfe-tooltip--Color, white);
  background-color: var(--pfe-tooltip--BackgroundColor, #151515);
  text-align: var(--pfe-tooltip__content--TextAlign, center);
  word-break: var(--pfe-tooltip__content--WordBreak, break-word);
  border-radius: var(--pfe-tooltip__content--BorderRadius, 0);
}

.pf-c-tooltip__content.pf-m-text-align-left {
  text-align: var(--pfe-tooltip__content--left--TextAlign, left);
}

.pf-c-tooltip__arrow {
  position: var(--pfe-tooltip__arrow--Postion, absolute);
  width: var(--pfe-tooltip__arrow--Width, 15px);
  height: var(--pfe-tooltip__arrow--Height, 15px);
  pointer-events: var(--pfe-tooltip--PointerEvents);
  background-color: var(--pfe-tooltip--BackgroundColor, #151515);
}

:host([theme="dark"]) {
  --pfe-tooltip--Color: #151515;
  --pfe-tooltip--BackgroundColor: #fff;
  --pfe-tooltip--BoxShadow: var(--pfe-tooltip--BoxShadow--dark, none);
}
